@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: inherit;
  font-family: inherit;
  color: inherit; }

html {
  box-sizing: border-box; }

body {
  font-family: "Catamaran", "Arial", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 24px;
  color: black;
  margin: 0;
  background-color: #222831; }

.wrapper {
  width: 930px;
  margin: 0 auto;
  padding: 0 30px;
  display: flex; }
  .wrapper--header {
    flex-direction: column; }
  .wrapper--main {
    position: relative; }

.page-main {
  padding-bottom: 100px;
  position: relative; }
  .page-main::after {
    content: "© Designed by SXRRYBRX";
    color: #B0B0B0;
    position: absolute;
    left: calc(50% - 60px);
    bottom: 0; }

.page-header {
  margin-top: 175px;
  margin-bottom: 73px; }

.page-header__subheading {
  margin: 0;
  padding: 0;
  order: -1; }

.page-header__heading {
  margin: 0;
  padding: 0;
  margin-top: -1px; }

.heading {
  font-weight: 600;
  font-size: 80px;
  line-height: 80px;
  color: #F2A365; }

.subheading {
  font-weight: 600;
  font-size: 22px;
  line-height: 33px;
  color: #F2A365;
  text-transform: uppercase; }

.slider {
  width: 100%; }

.slider__wrapper {
  display: flex;
  flex-direction: column;
  overflow: hidden; }

.slider__slides {
  width: 1760px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: flex-start; }

.slider__controls {
  width: 100%;
  margin-top: 32px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  order: 2; }

.slider__radio {
  display: none; }

.slider__button {
  width: 435px;
  height: 121px;
  font-weight: 400;
  font-size: 32px;
  color: #B0B0B0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer; }
  .slider__button::before {
    content: "";
    width: 100%;
    height: 1px;
    position: absolute;
    top: 4px;
    background-color: #30475E;
    transition: 200ms all ease-in-out; }
  #about:checked ~ .slider__controls .slider__button:first-child {
    color: #FFFFFF; }
    #about:checked ~ .slider__controls .slider__button:first-child::before {
      height: 7px;
      top: 1px;
      background-color: #FF7D7D; }
  #projects:checked ~ .slider__controls .slider__button:last-child {
    color: #FFFFFF; }
    #projects:checked ~ .slider__controls .slider__button:last-child::before {
      height: 7px;
      top: 1px;
      background-color: #FF7D7D; }

.slider__button-text {
  margin-top: 3px;
  margin-left: -8px;
  transition: 300ms all ease-in-out; }
  #about:checked ~ .slider__controls .slider__button:first-child .slider__button-text {
    margin-top: -15px; }
  #projects:checked ~ .slider__controls .slider__button:last-child .slider__button-text {
    margin-top: -15px; }
  .slider__button-text--margin {
    margin-left: 10px; }

.slide-about {
  width: 870px;
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-shrink: 0;
  transition: 200ms all ease-in-out; }
  #projects:checked ~ .slider__slides .slide-about {
    transform: translateX(-870px); }
  #about:checked ~ .slider__slides .slide-about {
    transform: translateX(0); }

.slide-about__photo {
  clip-path: url(#clip_path); }

.slide-about__paragraph {
  width: 573px;
  margin: 0;
  padding: 0;
  margin-top: 20px;
  margin-left: 60px;
  font-weight: 400;
  font-size: 22px;
  line-height: 33px;
  color: #B0B0B0; }

.slide-about__list {
  max-height: 100px;
  list-style: none;
  display: flex;
  flex-direction: row;
  transform: translate(258px, -63px); }

.slide-about__item {
  margin: 0;
  padding: 0; }
  .slide-about__item:first-child {
    margin-right: 150px; }

.slide-about__link {
  padding-left: 30px;
  text-decoration: none;
  font-weight: 400;
  font-size: 22px;
  color: #F2A365;
  position: relative; }
  .slide-about__link:hover::after {
    opacity: 1; }
  .slide-about__link::after {
    content: "";
    width: 75%;
    height: 2px;
    background-color: #FF7D7D;
    position: absolute;
    bottom: 0;
    right: 8px;
    opacity: 0;
    transition: 200ms all ease-in-out; }
  .slide-about__link::before {
    content: "";
    width: 27px;
    height: 27px;
    position: absolute;
    left: 0;
    top: 4px;
    background-repeat: no-repeat; }
  .slide-about__link--document::before {
    left: -1px;
    background-image: url("../img/icon-document.svg"); }
  .slide-about__link--github::before {
    left: -8px;
    background-image: url("../img/icon-github-small-orange.svg"); }
  .slide-about__link--github::after {
    width: 78%;
    right: 6px; }

.slide-projects {
  width: 900px;
  flex-shrink: 0;
  transition: 200ms all ease-in-out;
  overflow-y: scroll; }
  #projects:checked ~ .slider__slides .slide-projects {
    transform: translateX(-870px); }
  #about:checked ~ .slider__slides .slide-projects {
    transform: translateX(100%);
    transform: translateX(870px); }

.slide-projects__list {
  max-height: 280px;
  padding-right: 40px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }

.slide-projects__item {
  margin-bottom: 37px; }
  .slide-projects__item:not(:nth-child(3n)) {
    margin-right: 19px; }

.slide-projects__link {
  max-width: 277px;
  min-height: 120px;
  padding-left: 14px;
  padding-top: 14px;
  text-decoration: none;
  border-radius: 10px;
  background-color: rgba(47, 53, 62, 0);
  display: flex;
  flex-direction: column; }
  .slide-projects__link:hover {
    background-color: #2f353e;
    transition: 100ms all ease-in-out; }

.slide-projects__title {
  margin-bottom: 8px;
  font-weight: 600;
  font-size: 22px;
  color: #F2A365;
  text-transform: uppercase; }

.slide-projects__paragraph {
  margin: 0;
  font-weight: 400;
  font-size: 22px;
  line-height: 33px;
  color: #B0B0B0; }

.contacts {
  width: 220px;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: -180px;
  right: 30px; }

.contacts__label {
  margin-bottom: 25px;
  font-weight: 600;
  font-size: 22px;
  text-transform: uppercase;
  color: #B0B0B0;
  align-self: flex-end; }

.contacts__list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.contacts__link {
  padding: 0;
  display: inline-block; }

.contacts__icon {
  width: 40px;
  height: 40px;
  display: block; }
  .contacts__icon:hover svg > path {
    fill: #FFFFFF; }
